<template>
  <div id="app" class="container">
    <header>
      <img src="/src/assets/logo.jpg" class="logo" />
    </header>
    <div class="content">
      <div class="button-container">
        <button @click="showJoke">Joke</button>
        <button @click="showQuote">Quote</button>
        <button @click="showMeme">Meme</button>
      </div>
      <div id="output" v-html="output"></div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      output: ''
    };
  },
  methods: {
    async showJoke() {
      try {
        const response = await fetch('https://official-joke-api.appspot.com/random_joke?api-key=491ecedda4714fceae44f614302a4be5');
        const joke = await response.json();
        this.output = `${joke.setup} - ${joke.punchline}`;
      } catch (error) {
        this.output = 'Failed to fetch a joke.';
      }
    },
    async showQuote() {
      try {
        const response = await fetch('https://api.api-ninjas.com/v1/quotes', {
          headers: { 'X-Api-Key': 'vl5FLLIYtTCY42hm7iNp/g==NlmsIgh671OneanK' } 
        });
        const quotes = await response.json();
        console.log(quotes)
        if (quotes && quotes.length > 0) {
          const randomQuote = quotes[Math.floor(Math.random() * quotes.length)];
          this.output = `<blockquote>"${randomQuote.quote}"</blockquote><footer>- ${randomQuote.author}</footer>`;
        } else {
          this.output = 'Failed to fetch a quote.';
        }
      } catch (error) {
        this.output = 'Failed to fetch a quote.';
      }
    },
    async showMeme() {
      try {
        const response = await fetch('https://api.apileague.com/retrieve-random-meme?api-key=491ecedda4714fceae44f614302a4be5');
        const data = await response.json();
        if (data && data.url) {
          this.output = `<img src="${data.url}" alt="Random Meme">`;
        } else {
          this.output = 'Failed to fetch a meme.';
        }
      } catch (error) {
        this.output = 'Failed to fetch a meme.';
      }
    }
  }
};
</script>

